@import url(https://fonts.googleapis.com/css2?family=Amiko&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{font-size:62.5%;box-sizing:border-box;background-color:#0A192F}*,*:before,*:after{box-sizing:inherit}body{font-size:1.6rem;overflow-x:hidden;-ms-overflow-style:none;scrollbar-width:none;overflow-y:scroll}body::-webkit-scrollbar{display:none}

.NB--container{height:70px;width:100%}.NB--inner_container{display:flex;align-items:center;justify-content:space-between;height:100%}.NB--home .NB--home__sub{display:flex;align-items:center}.NB--home a{font-family:"Roboto Mono",monospace;font-size:15px;font-weight:300;letter-spacing:2px;text-decoration:none;color:#00FFF0;border:2px solid #00FFF0;border-radius:10px;padding:8px}.NB--home .NB--home__translate{margin-left:3vw}.NB--home .NB--home__translate :hover{cursor:pointer}.NB--navigation nav{display:flex}.NB--navigation a{font-family:"Amiko",sans-serif;font-size:20px;margin:0 2vw;text-decoration:none;color:white;justify-items:center}.NB--navigation a:last-child{margin-right:0px}.NB--navigation .NB--navigation__items{display:flex;align-items:center}.NB--navigation p{padding-left:1rem}@media only screen and (min-width: 390px) and (max-width: 767px){.NB--container{height:40px}.NB--home a{font-size:8px;border-radius:7px}.NB--home__translate svg{height:15px;width:17px}.NB--navigation a{font-size:10px}.NB--navigation svg{height:15px;width:15px}}

.HM--container{width:100%;height:1000px;align-items:center}.HM--container_about{width:60%;height:600px}.HM--container_about p,.HM--container_about h2,.HM--container_about h3{font-family:"Roboto Mono",monospace}.HM--container_about p{font-size:24px;color:#00FFF0}.HM--container_about h1{font-family:"Roboto",sans-serif;font-size:48px;color:#CCD6F6}.HM--container_about h2{font-size:48px;font-weight:bold;color:#8C96B8}.HM--container_about h3{font-size:24px;font-weight:bold;color:#8C96B8}.HM--container_phrase{text-align:center;width:570px}.HM--container_phrase p{font-family:"Roboto Mono",monospace;font-size:36px;color:#CCD6F6}@media only screen and (min-width: 390px) and (max-width: 767px){.HM--container{height:550px}.HM--container_about{height:300px;width:100%;text-align:center}.HM--container_about p{font-size:12px}.HM--container_about h1,.HM--container_about h2{font-size:18px}.HM--container_about h3{font-size:14px;width:80%;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.HM--container_phrase p{width:300px;font-size:18px;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.HM--container_phrase svg{width:60px;height:70px}}

.SK--container{width:100%;height:1170px;align-items:center;color:#57b2e7}.SK--divider{text-align:center;margin:0 120px}.SK--divider hr{margin-top:80px;border:0;border-top:5px solid #47618A;border-radius:10px}.SK--divider p{width:395px;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-family:"Roboto Mono",monospace;font-weight:500;font-size:48px;color:white}.SK--items{margin:0 115px}@media only screen and (min-width: 390px) and (max-width: 767px){.SK--container{height:330px;margin-bottom:100px}.SK--divider{margin:0 20px}.SK--divider hr{margin-top:29px;border-top:3px solid #47618A}.SK--divider p{width:130px;font-size:18px}.SK--items{margin:0 0}}@media only screen and (min-width: 834px) and (orientation: portrait){.SK--container{height:1350px}.SK--divider{margin:0 60px}.SK--divider p{width:310px}.SK--items{margin:0 0}}@media only screen and (min-width: 1112px) and (orientation: landscape){.SK--container{height:1320px}}

.SCM--container{width:130px;height:120px;border-radius:10px;background-color:#152C53;font-family:"Roboto",sans-serif;text-align:center}.SCM--inner-container{height:100%;padding:5px 0 0 0}.SCM--inner-container h1{width:115px;font-weight:bold;font-size:12px;color:white;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);padding-bottom:10px}.SCM--inner-container h2{font-weight:normal;font-size:9px;color:white;padding-bottom:10px;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.SCM--inner-container img{width:12px}.SCM--desc{width:110px;height:40px;left:50%;-webkit-transform:translateX(10%);transform:translateX(10%);padding-bottom:10px}

.SCD--container{width:310px;height:520px;border-radius:35px;background-color:#152C53;font-family:"Roboto",sans-serif;text-align:center}.SCD--inner-container{height:100%;padding:0 34px}.SCD--inner-container h1{font-weight:bold;font-size:24px;color:white}.SCD--inner-container h2{font-weight:normal;font-size:19px;color:white;padding-bottom:10px;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.SCD--inner-container hr{border:0;border-top:3px solid #47618A;border-radius:10px}.SCD--desc{height:117px}.SCD--AUX{display:flex;width:193px;height:38px;margin-bottom:25px}.SCD--AUX p{font-family:"Roboto",sans-serif;font-size:19px;color:white;position:relative;top:-30%}

.PC--container{width:350px;height:350px;background-color:#152C53;border-radius:15px}.PC--inner-container{height:100%;padding:15px 34px}.PC--icons{display:flex;justify-content:space-between;align-items:center}.PC--icons__github :hover{cursor:pointer;fill:#00FFF0}.PC--text{margin-top:-12px}.PC--text h1,.PC--text h2{font-family:"Roboto Mono",monospace;font-weight:normal}.PC--text h1{font-size:34px;height:40px;color:white}.PC--text h2{font-size:18px;height:90px;color:#A2A9C2;margin-top:-10px}.PC--status{text-align:center}.PC--status svg{height:35px}.PC--status h1{font-family:"Roboto Mono",monospace;margin-top:2px}.PC--techs{display:flex;justify-content:space-between;margin:0 10px;margin-top:-0.5vw}.PC--techs p{font-family:"Roboto Mono",monospace;font-weight:300;font-size:14px;color:#A2A9C2}@media only screen and (min-width: 390px) and (max-width: 767px){.PC--container{width:150px;height:150px;border-radius:10px}.PC--inner-container{padding:5px 10px}.PC--icons__folder svg{width:15px;height:13.13px}.PC--icons__github svg{width:15px;height:15px}.PC--text{margin-top:-7px}.PC--text h1{font-size:14px;height:18px}.PC--text h2{font-size:7px;height:38px;margin-top:-5px}.PC--status svg{height:15px}.PC--status h1{font-size:10px}.PC--techs p{font-size:6px}}

.PR--container{width:100%;height:1000px;align-items:center;color:#57b2e7}.PR--divider{text-align:center;margin:0 120px}.PR--divider hr{margin-top:80px;border:0;border-top:5px solid #47618A;border-radius:10px}.PR--divider p{width:395px;position:relative;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-family:"Roboto Mono",monospace;font-weight:500;font-size:48px;color:white}.PR--items{margin:0 50px}@media only screen and (min-width: 390px) and (max-width: 767px){.PR--container{height:470px}.PR--divider{margin:0 20px}.PR--divider hr{margin-top:29px;border-top:3px solid #47618A}.PR--divider p{width:130px;font-size:18px}.PR--items{margin:0 0}}@media only screen and (min-width: 834px) and (orientation: portrait){.PR--divider{margin:0 60px}.PR--divider p{width:310px}.PR--items{margin:0 0}}

